import jsPDF from "jspdf";
import "jspdf-autotable";
import { getToday, localStoragedecryp } from "../../../../../components/helpers/function";
// Date Fns is used to format the dates we receive

// define a generatePDF function that accepts a tickets argument
const ExportTransaksiPenjualan = (
  data = "",
  username = "",
  tanggal_dari = "",
  tanggal_sampai = ""
) => {
  // initialize jsPDF
  const doc = new jsPDF("l", "mm", [297, 210]);
  //   let data = JSON.parse(localStorage.getItem("tt_pengeluaran_barang")) || [];
  let tableRows = [];
  // let footRows = [];
  let finalY = 30;
  let total_barang = 0;
  let total_berat_footer = 0;
  let total_onkos_footer = 0;
  let total_harga_jual_footer = 0;
  let total_berat_atribut_footer = 0;
  let total_harga_atribut_footer = 0;
  let total_harga_total_footer =0
  let total_harga_gram_footer=0
  let total_baraang=0


  doc.setFontSize(15);
  doc.text("LAPORAN TRANSAKSI PENJUALAN", 14, 15);
  doc.setFontSize(10);
  doc.setProperties({
    title: "LAPORAN TRANSAKSI PENJUALAN",
  });
  //row 1
  doc.text(
    `PERIODE : ${
      localStoragedecryp("tgl_laporan").length === 0
        ? null
        : JSON.parse(localStoragedecryp("tgl_laporan")).tgl_awal +
          " s/d " +
          JSON.parse(localStoragedecryp("tgl_laporan")).tgl_akhir
    }`,
    14,
    25
  );
  let tableColumn = [
    // [
    //   {
    //     content: `No Faktur Group : ${item._id.no_faktur_group}`,
    //     colSpan: 10,
    //   },
    // ],
    [
      {
        content: `NO FJ`,
      },
      {
        content: `KODE BARCODE`,
      },
      {
        content: `KODE MEMBER`,
      },
      {
        content: `NAMA MEMBER`,
      },
      {
        content: `NAMA BARANG`,
      },
      {
        content: `BERAT`,
      },
      {
        content: `ONGKOS`,
      },
      {
        content: `HARGA JUAL`,
      },
      {
        content: `NAMA ATRIBUT`,
      },
      {
        content: `BERAT ATRIBUT`,
      },
      {
        content: `HARGA ATRIBUT`,
      },
      {
        content: `HARGA GRAM`,
      },
      {
        content: `HARGA TOTAL`,
      },
    ],
  ];
  data.forEach((item, index) => {
    let tot_berat = 0;
    let tot_onkos = 0;
    let tot_harga_jual = 0;
    let tot_harga_gram = 0;
    let tot_berat_atribut = 0;
    let tot_harga_atribut = 0;
    let tot_harga_total = 0;
    // let data = [
    //   {
    //     content: `No Faktur Group : ${item._id.no_faktur_group}`,
    //     colSpan: "10",
    //   },
    // ];
    // tableRows.push(data);
    item.detail.forEach((item, index) => {
     
      let rows = [
        item.no_faktur_jual,
        item.kode_barcode,
        item.kode_member,
        item.nama_customer,
        item.nama_barang,
        item.berat.toFixed(3),
        parseInt(item.ongkos).toLocaleString("ID-id"),
        parseInt(item.harga_jual).toLocaleString("ID-id"),
        item.nama_atribut,
        item.berat_atribut.toFixed(3),
        parseInt(item.harga_atribut).toLocaleString("ID-id"),
        parseInt(item.harga_gram).toLocaleString("ID-id"),
        parseInt(item.harga_total).toLocaleString("ID-id"),
      ];

      total_berat_footer = total_berat_footer + parseFloat(item.berat);
      total_onkos_footer = total_onkos_footer + parseInt(item.ongkos);
      total_harga_jual_footer = total_harga_jual_footer + parseInt(item.harga_jual);
      total_berat_atribut_footer = total_berat_atribut_footer + parseInt(item.berat_atribut);
      total_harga_atribut_footer = total_harga_atribut_footer + parseInt(item.harga_atribut);
      total_harga_gram_footer = total_harga_gram_footer + parseInt(item.harga_gram);
      total_harga_total_footer = total_harga_total_footer + parseInt(item.harga_total);



      tot_berat = tot_berat + parseFloat(item.berat);
      tot_onkos = tot_onkos + parseInt(item.ongkos);
      tot_harga_jual = tot_harga_jual + parseInt(item.harga_jual);
      tot_berat_atribut = tot_berat_atribut + parseInt(item.berat_atribut);
      tot_harga_atribut = tot_harga_atribut + parseInt(item.harga_atribut);
      tot_harga_gram = tot_harga_gram + parseInt(item.harga_gram);
      tot_harga_total = tot_harga_total + parseInt(item.harga_total);
      tableRows.push(rows);
    });
    total_baraang = total_baraang + item.detail.length;
    
    // localStorage.setItem('hasil',JSON.stringify(item.detail.length))
    // console.log();
    let total = [
      {
        content: `Total Barang : ${item.detail.length}`,
        colSpan: 5,
        styles: {
          halign: 'center',
          fillColor: "#E8E5E5",
          textColor: "#000",
        },
      },
      {
        content: `${tot_berat.toFixed(3)}`,
        styles: {
          fillColor: "#E8E5E5",
          textColor: "#000",
        },
      },
      {
        content: `${parseInt(tot_onkos).toLocaleString("ID-id")}`,
        styles: {
          fillColor: "#E8E5E5",
          textColor: "#000",
        },
      },
      {
        content: `${parseInt(tot_harga_jual).toLocaleString("ID-id")}`,
        styles: {
          // halign: 'center',
          fillColor: "#E8E5E5",
          textColor: "#000",
        },
      },
      {
        content: ``,
        styles: {
          // halign: 'center',
          fillColor: "#E8E5E5",
          textColor: "#000",
        },
      },
     
      {
        content: `${tot_berat_atribut.toFixed(3)}`,
        styles: {
          // halign: 'center',
          fillColor: "#E8E5E5",
          textColor: "#000",
        },
      },
      {
        content: `${parseInt(tot_harga_atribut).toLocaleString("ID-id")}`,
        styles: {
          // halign: 'center',
          fillColor: "#E8E5E5",
          textColor: "#000",
        },
      },
      {
        content: `${parseInt(tot_harga_gram).toLocaleString("ID-id")}`,
        styles: {
          // halign: 'center',
          fillColor: "#E8E5E5",
          textColor: "#000",
        },
      },
      {
        content: `${parseInt(tot_harga_total).toLocaleString("ID-id")}`,
        styles: {
          // halign: 'center',
          fillColor: "#E8E5E5",
          textColor: "#000",
        },
      },
    ];
    tableRows.push(total);
    total_barang = total_barang + item.detail.length;
  });
  let footer = [
    {
      content: `Grand Barang : ${total_baraang}`,
      colSpan: 5,
      styles: {
        halign: 'center',
        fillColor: "#E8E5E5",
        textColor: "#000",
      },
    },
    {
      content: `${total_berat_footer.toFixed(3)}`,
      styles: {
        fillColor: "#E8E5E5",
        textColor: "#000",
      },
    },
    {
      content: `${parseInt(total_onkos_footer).toLocaleString("ID-id")}`,
      styles: {
        fillColor: "#E8E5E5",
        textColor: "#000",
      },
    },
    {
      content: `${parseInt(total_harga_jual_footer).toLocaleString("ID-id")}`,
      styles: {
        fillColor: "#E8E5E5",
        textColor: "#000",
      },
    },
    {
      content: ``,
      styles: {
        fillColor: "#E8E5E5",
        textColor: "#000",
      },
    },
    {
      content: `${total_berat_atribut_footer.toFixed(3)}`,
      styles: {
        fillColor: "#E8E5E5",
        textColor: "#000",
      },
    },
   
    {
      content: `${parseInt(total_harga_atribut_footer).toLocaleString("ID-id")}`,
      styles: {
        fillColor: "#E8E5E5",
        textColor: "#000",
      },
    },
    {
      content: `${parseInt(total_harga_gram_footer).toLocaleString("ID-id")}`,
      styles: {
        fillColor: "#E8E5E5",
        textColor: "#000",
      },
    },
    {
      content: `${parseInt(total_harga_total_footer).toLocaleString("ID-id")}`,
      styles: {
        fillColor: "#E8E5E5",
        textColor: "#000",
      },
    },
  ];
  tableRows.push(footer);

  let printed = [
    {
      content: `Printed By ${JSON.parse(localStoragedecryp("userdata")).user_id} / ${getToday()}`,
      colSpan: 13,
      styles: {
        // lineWidth: 0.1,
        fontStyle: "italic",
        textColor: "#000",
        // fillColor: "#E8E5E5"
      },
    },
  ]
  tableRows.push(printed);
  
  doc.autoTable({
    head: tableColumn,
    body: tableRows,
    // foot: footRows,
    startY: finalY,
    theme: "plain",
    rowPageBreak: "avoid",
    // pageBreak: "avoid",
    margin: { top: 10 },
    footStyles: {
      // lineWidth: 0.02,
      // lineColor: "#000",
      fontSize: 8,
    },
    bodyStyles: {
      // lineWidth: 0.02,
      // lineColor: "#000",
      fontSize: 8,
    },
    headStyles: {
      fontSize: 8,
      // lineWidth: 0.02,
      // lineColor: "#000",
      textColor: "#000",
      fillColor: "#E8E5E5",
    },
    tableLineColor: [255, 255, 255],
    tableLineWidth: 1,
  });
  finalY = doc.autoTableEndPosY() + 3;
  tableRows = [];
  // footRows = [];
  // tot_berat = 0;
  // tot_onkos = 0;
  // tot_harga_jual = 0;
  // tot_harga_gram = 0;
  // tot_berat_atribut = 0;
  // tot_harga_atribut = 0;
  // tot_harga_total = 0;
  // const date = Date().split(" ");
  // we use a date string to generate our filename.
  // const dateStr = date[2] + date[3] + date[4];
  // ticket title. and margin-top + margin-left
  // doc.text(`User                           : ${JSON.parse(localStoragedecryp("userdata")).user_id}`, 14, finalY + 10);
  // doc.text(`Cetak                        : ${getToday()}`, 14, finalY + 16);
  // doc.text(`Total Barang Terjual : ${total_barang}`, 14, finalY + 20);
  const pages = doc.internal.getNumberOfPages();
  const pageWidth = doc.internal.pageSize.width; //Optional
  const pageHeight = doc.internal.pageSize.height; //Optional
  doc.setFontSize(10); //Optional
  for (let j = 1; j < pages + 1; j++) {
    let horizontalPos = pageWidth / 2; //Can be fixed number
    let verticalPos = pageHeight - 10; //Can be fixed number
    doc.setPage(j);
    doc.text(`${j} of ${pages}`, horizontalPos, verticalPos, {
      align: "center",
    });
  }
  // doc.autoPrint();
  // doc.save(`LAPORAN TRANSAKSI PENJUALAN.pdf`);
  var string = doc.output("datauristring");
  // var embed = "<embed width='100%' height='100%' src='" + string + "'/>";
  var x = window.open();
  x.document.open();
  x.document.write(
    "<html><head><title>LAPORAN TRANSAKSI PENJUALAN</title></head><body style='margin:0 !important'><embed width='100%' height='100%'  src=" +
      string +
      "></embed></body></html>"
  );
  // x.document.write(embed);
  // setInterval(() => {
  //   x.close();
  // }, 1000);
};

export default ExportTransaksiPenjualan;
